<template>
  <div>
    <Event  :EventType='event' :breadcrumbs="[ { text: 'Home', disabled: false, href: '/',}, { text: 'Races', disabled: true, }, ]"/>
  </div>
</template>

<script>
import Event from "../components/Event.vue";

export default {
  components: {
    Event,
  },
  data(){
      return{
          event: '',
          mountStatus: true
      }
  },
  created(){
    console.log("Mounted")
  },
  watch:{
    $route(value){
        value
        if(this.$route.params.id){
          console.log("Mounted")
          if(this.$route.params.id == 'raceOnly'){
              this.event = 54
          }
          else if(this.$route.params.id == 'all'){this.event = ''}
          else if(this.$route.params.id == 'MTB'){this.event = 59}
          else if(this.$route.params.id == 'GravelEvents'){this.event = 71}
          else if(this.$route.params.id == 'GranFondo'){this.event = 70}
          else if(this.$route.params.id == 'FunRideTour'){this.event = 72}
          else if(this.$route.params.id == 'Meeting'){this.event = 50}
          else if(this.$route.params.id == 'CROSSCLINIC'){this.event = 61}
          else{
              this.event =this.$route.params.id
          }
          
      }
    }
  },
//   mounted(){
//       if(this.$route.params.id){
//           console.log("Mounted")
//           if(this.$route.params.id == 'raceOnly'){
//               this.$route.params.id = 54
//           }
//           else{
//               this.event =this.$route.params.id
//           }
          
//       }
//   }

};
</script>

