<template>
  <div>
    <v-breadcrumbs :items="breadcrumbs"></v-breadcrumbs>
    <v-row justify="center">
      <v-col cols="12" sm="3">
        <v-date-picker
          class="ma-3"
          v-model="picker"
          type="month"
        ></v-date-picker>
      </v-col>
      <v-col cols="12" sm="9">
        <div v-if="loading" class="text-center">
          <v-progress-circular
            size="25"
            indeterminate
            color="primary"
          ></v-progress-circular>
        </div>
        <v-simple-table v-else fixed-header height="450px">
          <template v-slot:default>
            <v-row v-if="UpComingEvents.length >= 1">
              <v-col
                cols="12"
                sm="3"
                class="ma-0 pa-0"
                v-for="i in UpComingEvents"
                v-bind:key="i.id"
              >
                <v-card class="ma-1" max-width="300" elevation="0">
                  <v-card-text>
                    <h3>{{ i.name }}</h3>
                    <v-row align="center" class="mx-0 mt-2">
                      <div v-if="i.promoter" class="grey--text ms-4">
                        Promoter: {{ i.promoter.firstname }}
                        {{ i.promoter.lastname }}
                      </div>
                    </v-row>

                    <div class="mt-2 text-subtitle-3">
                      {{ i.eventdatetime_format }}
                    </div>
                  </v-card-text>

                  <v-card-actions>
                    <a v-if="i.url" :href="i.url">Promoter Url</a>
                  </v-card-actions>
                </v-card>
              </v-col>
            </v-row>
            <b v-else>No Event Found</b>
          </template>
        </v-simple-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import CycAxios from "../respo";

export default {
  watch: {
    picker(value) {
      //  console.log(value.split('-'))
      this.UpComingEvents = [];
      this.getUpcomingEvents(value.split("-")[0], value.split("-")[1]);
    },
    EventType(value){
        value
        console.log("Event changed", value)
        this.getUpcomingEvents(
      this.picker.split("-")[0],
      this.picker.split("-")[1]
    );    
    }
  },
  data() {
    return {
      loading: true,
      picker: "2022-01",
      UpComingEvents: [],
    };
  },
  methods: {
    getUpcomingEvents(year, month) {
      this.loading = true;
      CycAxios.get(
        `/AcaEventNoPage/?ordering=-eventdatetime&eventtype=${this.EventType}&eventdatetime__year=${year}&eventdatetime__month=${month}`
      ).then((data) => {
        this.loading = false;
        this.UpComingEvents = data.data;
      });
    },
  },
  mounted() {
    this.getUpcomingEvents(
      this.picker.split("-")[0],
      this.picker.split("-")[1]
    );
  },
  props:{
      EventType: {
          default: ''
      },
      breadcrumbs :{
          default: [
        {
          text: "Home",
          disabled: false,
          href: "/",
        },
        {
          text: "Event View",
          disabled: true,
        },
      ]
      }
  }
};
</script>

